
















































import { Vue, Component } from 'vue-property-decorator';
import CustomizedFooter from 'components/customized-footer.vue';
import CustomizedNav from 'components/customized-nav.vue';

declare type routeTypeAdvanced = {
  path?: any,
  label: string,
  icon?: string,
  isMenu?: boolean,
  children?: {
    path: string,
    label: string,
    icon?: string,
  }[],
};

const subRoutes: routeTypeAdvanced[] = [{
  path: '/resume/info',
  label: '简历总览',
  icon: '&#xe61d;',
  isMenu: true,
}, /* {
  path: '/resume/enterprise',
  label: '企业人才库',
  icon: '&#xe61e;',
  isMenu: true
}, {
  label: '平台人才库',
  icon: '&#xe609;',
  path: '/resume/platform',
  isMenu: true,
  children: [{
    path: '/resume/platform',
    label: '人才列表',
  }, {
    path: '/resume/platform/collection',
    label: '收藏夹',
  }, {
    path: '/resume/platform/downloaded',
    label: '已下载简历',
  }]
}, {
  path: '/resume/blacklist',
  label: '黑名单',
  icon: '&#xe604;',
  isMenu: true
}, */];

@Component({
  components: {
    CustomizedFooter,
    CustomizedNav,
  },
})
export default class Enterprise extends Vue {
  currentRoute: string = '';

  activedMenu: string = '/resume/info';

  subRoutes = subRoutes;

  syncRoute(source: string) {
    this.currentRoute = source;
    this.activedMenu = this.currentRoute;
  }

  mounted() {
    // init state;
    this.syncRoute(this.$route.path);
  }

  updated() {
    // update state;
    this.syncRoute(this.$route.path);
  }

  toggleActivedMenu(e: string) {
    this.syncRoute(e);
  }
}
